}

.ForgotPasswordLeftWrapper {
    margin-top: 2rem;
    width: 55%;
    padding: 0rem 2rem;
}

.ForgotPasswordIntro {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 40px;
    display: flex;
    align-items: center;
    font-size: 2rem;
    /* line-height: 40px; */
    font-weight: 700;
    margin-bottom: 1rem;
    font-family: "Poppins", sans-serif;
}
.ForgotPasswordIntroSubHeading {
    color: gray;
    font-size: 1.2rem;
    margin-bottom: 1rem;
