.emailMeBtn{
    cursor: pointer;
    color: white;
    padding: 7px 0px;
    background-color: #C1C7D2;
    width: 100%;
    font-size: 1rem;
    line-height: 24px;
    font-weight: 600;
    border-radius: 100px;
    border: none;
    outline: none;
}
.emailMeBtn:focus{
    outline: none;
}

