}

.loading {
    display: flex;
    min-height: 500px;
    align-items: center;
    justify-content: center;
}

.userImg {
    border-radius: 100px;
    display: block;
    height: 100px;
    margin: 0 auto;
    width: 100px;
}

.hero .app-logo {
    max-width: 10.5rem;
}

